(() => {
  const reviewsItems = document.querySelectorAll(`.reviews__swiper-slide`);
  const reviewsInputs = document.querySelectorAll(`.reviews__blockqute-wrap input`);
  const reviewsRatings = document.querySelectorAll(`.reviews__ratings`);

  if (reviewsItems) {
    for (let i = 0; i < reviewsItems.length; i++) {
      for (let k = 0; k < reviewsInputs[i].value; k++) {
        reviewsRatings[i].children[k].style.color = `#ffb200`;
      }
    }
  }
})();
